/*
* Style Works for both Editor and Frontend
*/

.wp-block-wtrb-boxslogans {
	box-sizing: border-box;
	display: grid;
	position: relative;
	* {
		margin: 0;
		padding: 0;
	}
	&.wtrbs__dcols_4 {
		grid-template-columns: repeat(4, 1fr);
		gap: var(--d-gap);
	}
	&.wtrbs__dcols_3 {
		grid-template-columns: repeat(3, 1fr);
		gap: var(--d-gap);
	}
	.wp-block-wtrb-boxsloganmember {
		overflow: hidden;
		transition: 0.3s;
		* {
			margin: 0;
		}
		.wtrbmr__slogan_link {
			display: inline-block;
			text-decoration: none;
			font-size: 0.9em;
			line-height: 1em;
		}
	}
	[data-type="wtrb/boxsloganmember"] {
		margin: 0 !important;
		padding: 0 !important;
	}
	.wtrbmr__slogan_container {
		position: relative;
		display: flex;
		flex-direction: column;
		background: #fff;
		&.wtrbmr__box_shadow {
			box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
		}
		.wtrbmr__slogan_card {
			background: #f4f4f4;
			display: flex;
			flex-direction: column;
			overflow: hidden;
			transition: 0.3s;

			.wtrbmr__slogan_photo {
				position: relative;
				width: 100%;

				.components-placeholder {
					height: 100%;
				}
				img {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
				}
			}

			.wtrbmr__slogan_photo {
				img {
					aspect-ratio: 1 / 1;
					object-fit: cover;
				}
			}

			/* Member Info - Container */
			.wtrbmr__slogan_content {
				position: relative;
				width: 100%;
				display: flex;
				justify-content: center;
				align-items: center;
				.wtrbmr__slogan_content_box {
					width: 100%;
				}
				.wtrbmr__slogan_description {
					color: #444444;
				}
			}
		}
	}
}

// Smartphone/Small devices
@media (max-width: 767px) {
	.wp-block-wtrb-boxslogans {
		.wtrbmr__slogan_container {
			.wtrbmr__slogan_card {
				flex-direction: column;
			}
		}
		.wp-block-wtrb-boxsloganmember:nth-child(2n) .wtrbmr__slogan_card {
			flex-direction: column-reverse;
		}
		&.wtrbs__mcols_1 {
			grid-template-columns: repeat(1, 1fr);
			grid-row-gap: var(--m-gap);
		}
	}
}

/* Medium/Tablet Devices */
@media (min-width: 768px) and (max-width: 1024px) {
	.wp-block-wtrb-boxslogans {
		.wtrbmr__slogan_container {
			.wtrbmr__slogan_card {
				flex-direction: row;
				.wtrbmr__slogan_photo {
					width: 50%;
				}
				.wtrbmr__slogan_content {
					width: 50%;
				}
			}
		}
		&.wtrbs__tcols_2 {
			grid-template-columns: repeat(auto-fit, minmax(100%, 1fr));
			grid-row-gap: var(--t-gap);
		}
	}
}

@media (min-width: 1025) {
	.wp-block-wtrb-boxslogans {
		&.wtrbs__dcols_4 {
			grid-template-columns: repeat(4, 1fr);
			gap: var(--d-gap);
		}
		&.wtrbs__dcols_3 {
			grid-template-columns: repeat(3, 1fr);
			gap: var(--d-gap);
		}
	}
}
